*:focus {
    outline: none;
}

ul, li {
    list-style: none;
    padding: 0;
}


/*disable number input arrows*/
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
input[type=number] {
    -moz-appearance:textfield;
}
/*****************************/


.loginPage {
    display: flex;
    width: 100%;
}

.loginPageLogo {
    flex: 1;
    height: 100vh;

    background: url(img/LoginLogo.png) center center no-repeat, #1C1A19;
}

.loginPageInputs {
    background: url(img/backgroundMap.png) no-repeat;
    background-size: cover;
    flex: 2;

    display: flex;
    justify-content: center;
    align-items: center;
}
  
.header {
    background: url(img/logo.png) left 2% top 50% no-repeat, #1C1A19;
    padding: 10px;
    display: flex;
    justify-content: flex-end;
}

.header__nav-list {
    display: flex;
}

.nav__button {
    background:none;
    border:none;
    margin:0;
    padding:0;
    cursor: pointer;

    color: #FFFFFF;
}

.MuiButton-label {
    color: #FFFFFF;
    font-weight: 400;
}
.MuiButton-label:hover {
    color: #FDBF5A;
}

.email.input {
    margin-bottom: 30px;
}

.map-wrapper {
    position: relative;
    width: 100%;
    height: 95vh;
}

.map {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.loginPanel {
    background: #FFFFFF;
    border-radius: 20px;
    width: 520px;
    min-height: 50%;

    padding: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.loginPanelTextFields {
    display: flex;
    flex-direction: column;

    margin-bottom: 20%;
}



.panelTitle {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    text-align: center;

    margin-top: 10%;
    margin-bottom: 5%;
}

.loginPanelForm {
    display: flex;
    flex-direction: column;

    width: 68%;
}



.submit.input {
    background: #FDBF5A;
    border-radius: 70px;
    border: none;
    padding: 16px;

    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 25px;

    margin-bottom: 7%;

    min-width: 250px;
}

.submit.input:hover {
    cursor: pointer;
}

.submit.input:focus {
    outline: none;
}

.panelSwitch {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.panelSwitchText {
    color: #828282;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;

    margin-right: 20px;
}

.panelSwitchButton {
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;

    color: #FDBF5A;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
}

.panelSwitchButton:focus {
    outline: none;
}

.MuiTextField-root.loginPanelInput {
    margin-bottom: 20px;
}

.MuiTextField-root.loginPanelInput:last-child {
    margin: 0;
}

.section {
    position: relative;
}

.profile {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
}

.modalBackground {
    width: 100%;
    height: 100%;

    background:rgba(28, 26, 25, 0.2);

    display: flex;

    justify-content: center;
    align-items: center;
}

.modal {
    min-width: 870px;
    width: 60%;
    height: 56%;

    background: #FFFFFF;

    border-radius: 10px;
    padding: 44px;

    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.modalTitle {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    text-align: center;

    margin-bottom: 10px;
}

.modalSubTitle {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    color: #7B7B7B;
    text-align: center;

    margin-bottom: 30px;
}

.modalContent {
    display: flex;
    justify-content: space-between;

    width: 100%;

    margin-bottom: 40px;
}

.modalInputs {
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}

.modalInputRow {
    display: flex;
    justify-content: space-between;
}

.MuiTextField-root.modalInput {
    margin-bottom: 20px;
}

.MuiTextField-root.modalInputSmall {
    width: 45%;
}


.card {
    width: 350px;
    height: 180px;

    border-radius: 10px;
    box-shadow: 0px 5px 20px 2px rgba(0, 0, 0, 0.1);

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 16px 16px 16px 28px;
}

.cardRow {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.cardDate {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
}

.cardNumber {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
}

.mastercardIcon {
    position: relative;
    width: 28px;
    height: 28px;
}

.mastercardIcon::before{
    content: '';
    position: absolute;
    right: 10px;
    background-color: rgba(230, 230, 230, 0.6);
    width: 28px;
    height: 28px;
    border-radius: 50%;
}

.mastercardIcon::after{
    content: '';
    position: absolute;
    left: 10px;
    background-color: rgba(230, 230, 230, 0.6);
    width: 28px;
    height: 28px;
    border-radius: 50%;
}


.address {
    min-width: 400px;
    min-height: 400px;
    position: fixed;
    top: 15%;
    left: 5%;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3%;
    border-radius: 10px;
    box-shadow: 0px 10px 20px -5px rgba(0, 0, 0, 0.1);
    background-color: #FFFFFF;

    
}

.addressForm {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}
.selectors {
    height: 50%;
    flex: 1.5;
    display: flex;
    flex-direction: column;
}

.selector {
    flex: 1;
    position: relative;
    margin-bottom: 30px;
    cursor: pointer;
}

.selector:last-child {
    margin-bottom: 0;
}
.selectorItem {
    border-bottom: 1px solid;
}


.selectors .selector {
    background: none;
    border: none;
    border-bottom: 1px solid #E0E0E0;
    
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    padding: 0 0 1% 5%;
}



.carsList {
    flex: 4;
    display: flex;
    justify-content: space-between;
}

.carCard {
    flex: 1;
    border-radius: 10px;
    box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.2);
    padding: 12px;
    margin-right: 20px;
}
.carCard:last-child {
    margin-right: 0;
}

.carTitle {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
}

.carPricePanel {
    display: flex;
    flex-direction: column;
}

.carPriceTitle {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    color: #828282;
}

.carPrice {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 24px;
}

.loginPanelInput {
    border: none;
    border-bottom: 1px solid #E0E0E0;
    margin-bottom: 30px;
    padding:  0 0 5px 5px;
    font-size: 18px;
}
.loginPanelInput:last-child {
    margin-bottom: 0;
}

.modalForm {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.modalInput {
    border: none;
    border-bottom: 1px solid #E0E0E0;
    margin-bottom: 30px;
}
.modalInputs {
    margin-bottom: 0;
}

.modalInputSmall {
    border: none;
    border-bottom: 1px solid #E0E0E0;
    margin-right: 10px;
}
.modalInputSmall:last-child {
    margin-right: 0;
}
